// общие стили
.form {
    width: 100%;
    margin: 0 auto;
    padding: 5rem 0;
    &__title {
        @include Nunito-Bold();
        font-style: normal;
        font-weight: bold;
        font-size: 2.6rem;

        line-height: 146.4%;
        text-align: left;
        width: 100%;
        max-width: 98.8rem;
        color: #ffffff;

        @media (min-width: 411px) {
            font-size: 3rem;
        }
        @media (min-width: 540px) {
            font-size: 3.6rem;
        }
        @media (min-width: 768px) {
            font-size: 4.4rem;
        }
        @media (min-width: 1024px) {
            align-self: flex-start;
        }
    }

    h3 {
        font-style: normal;
        font-weight: bold;
        font-size: 2.2rem;
        line-height: 3.5rem;
        color: rgba(255, 255, 255, 0.8);
        text-align: left;
        width: 100%;
        margin-bottom: 3rem;
        @media (min-width: 411px) {
            font-size: 2.6rem;
        }
    }
    form {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @media (min-width: 800px) {
            flex-direction: row;
            flex-wrap: wrap;
        }
        @media (min-width: 1200px) {
            justify-content: flex-start;
        }

        input,
        .btn-red {
            @include Nunito-Bold();
            font-style: normal;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;
            outline: none;
            width: 100%;
            max-width: 35.4rem;
            @media (min-width: 411px) {
                max-width: 35.4rem;
                min-width: 35.4rem;
            }
            @media (min-width: 1200px) {
                margin-bottom: 0;
            }
        }

        input {
            border: 1px solid #f04242;
            box-sizing: border-box;
            border-radius: 100px;
            background: transparent;
            padding: 1.3rem 3.5rem;
            margin-bottom: 3rem;
            color: rgba(255, 255, 255, 0.7);

            &:first-child {
                @media (min-width: 800px) {
                    margin-right: 3rem;
                }
                @media (min-width: 1050px) {
                    margin-right: 2rem;
                    margin-left: 2rem;
                }
                @media (min-width: 1200px) {
                    margin-right: 3rem;
                    margin-left: 0;
                }
            }
            @media (min-width: 1050px) {
                margin-right: 2rem;
                margin-left: 2rem;
            }
            @media (min-width: 1200px) {
                margin-right: 3rem;
                margin-left: 0;
            }
        }
        ::-webkit-input-placeholder {
            @include Nunito-Bold();
            font-style: normal;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;
            color: rgba(255, 255, 255, 0.7);
        }
        ::-moz-placeholder {
            @include Nunito-Bold();
            font-style: normal;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;
            color: rgba(255, 255, 255, 0.7);
        } /* Firefox 19+ */
        :-moz-placeholder {
            @include Nunito-Bold();
            font-style: normal;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;
            color: rgba(255, 255, 255, 0.7);
        } /* Firefox 18- */
        :-ms-input-placeholder {
            @include Nunito-Bold();
            font-style: normal;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;
            color: rgba(255, 255, 255, 0.7);
        }
        .btn-red {
            padding: 1.3rem 3.5rem;

            @media (min-width: 1200px) {
                max-width: 23.2rem;
                min-width: 23.2rem;
            }
        }
    }
}
