}
.form-prices--stock {
    padding: 0 0 10rem 0;
    .form {
        background: #ebf7f5;
        box-shadow: 0px 5px 10px rgba(1, 18, 15, 0.05);
        border-radius: 20px;
        padding-left: 2rem;
        padding-right: 2rem;
        position: relative;
        &:after {
            position: absolute;
            content: "";
            width: 100%;
            max-width: 49rem;
            height: 13rem;
            max-height: 29rem;
            top: 0;
            left: 0;
            background: url("../img/icons/marc.svg");
            background-size: cover;
            background-position: center;
            z-index: -1;
            transform: translate(-0.5rem, -5rem);
            @media (min-width: 1200px) {
                transform: translate(-5rem, -5rem);
            }
        }
        @media (min-width: 1200px) {
            padding-left: 5rem;
            padding-right: 5rem;
        }
        h2 {
            color: #01120f;
        }
        h3 {
            color: rgba(1, 18, 15, 0.8);
        }
        label,
        a {
            font-style: normal;
            font-weight: normal;
            font-size: 1.4rem;
            line-height: 2.5rem;
            color: rgba(1, 18, 15, 0.8);
            text-align: left;
            width: 100%;
        }
        a {
            color: rgba(1, 18, 15, 0.8);
            text-decoration: underline;
        }
    }
    form {
        @media (min-width: 800px) {
            flex-direction: column;
        }
        @media (min-width: 900px) {
            flex-direction: row;
        }
        input {
            color: rgba(240, 66, 66, 0.7);
            &:first-child {
                @media (min-width: 800px) {
                    margin-right: 0;
                }
                @media (min-width: 900px) {
