.advantages-appeal {
    width: 100%;
    padding-bottom: 10rem;
    //     // padding: 8rem 0;
    //     text-align: left;
    //     p {
    //         width: 100%;
    //     }
    p {
        margin-bottom: 0.8rem;
        &:last-child {
            margin-bottom: 0;
        }
        a {
            font-style: normal;
            font-weight: normal;
            font-size: 1.6rem;
            line-height: 170%;
            color: #01120f;
            display: inline-block;
            position: relative;
            transition: all 0.3s;

            &::before {
                position: absolute;
                content: "";
                bottom: 0;
                left: 0;
                width: 100%;
                height: 0.1rem;
                background-color: #11c0ab;
                transform: scaleX(1);
                transition: transform 0.5s;
            }
            &:hover,
            &:focus {
                transition: all 0.3s;
                text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px,
                    rgba(17, 192, 171, 0.4) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px;
                &::before {
                    transform: scaleX(0);
                }
            }
        }
    }
    ul {
        width: 100%;
        padding-left: 2rem;
        li {
            width: 100%;
            list-style: disc;
        }
    }
}
// .an {
//     &::after {
//         display: none;
//     }
// }
.advantages-prices {
    padding-bottom: 10rem;
    // padding: 0 0 10rem 0;
    h1 {
        margin-bottom: 5rem;
    }
    h2 {
        text-align: left;
        margin-bottom: 3rem;
        width: 100%;
        padding-left: 0;
        &::after {
            display: none;
        }
    }
    p {
        margin-bottom: 0.8rem;
        width: 100%;
        i {
            color: rgba(1, 18, 15, 0.8);
        }
    }
    .info {
        margin-bottom: 3rem;
    }
    ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @media (min-width: 900px) {
            flex-direction: row;
            flex-wrap: wrap;
            align-items: stretch;
        }
        li {
            border: 1px solid #11c0ab;
            box-sizing: border-box;
            border-radius: 20px;
            width: 100%;
            max-width: 37rem;
            padding: 0rem 3rem 3rem 3rem;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            margin-bottom: 3rem;
            transition: all 0.4s;
            &:hover,
            &:focus {
                transition: all 0.4s;
                transform: scale(1.03, 1.03);
                box-shadow: 0px 5px 10px rgba(17, 192, 171, 0.5);
            }
            @media (min-width: 900px) {
                margin-right: 3rem;
            }

            &:nth-child(2n) {
                @media (min-width: 900px) {
                    margin-right: 0rem;
                }
                @media (min-width: 1200px) {
                    margin-right: 3rem;
                }
            }
            &:nth-child(3n) {
                @media (min-width: 1200px) {
                    margin-right: 0rem;
                }
            }
            &:last-child {
                @media (min-width: 1200px) {
                    margin-right: 0;
                }
            }
            p {
                &:last-child {
                    @media (min-width: 411px) {
                        display: flex;
                        justify-content: flex-start;
                        align-items: center;
                    }
                }
                button {
                    max-width: 13rem;
                    min-width: 13rem;
                    margin-top: 2rem;
                    display: block;
                    @media (min-width: 411px) {
                        margin-top: 0rem;
                        margin-left: auto;
                    }
                }
            }
        }
        h3 {
            padding: 1.6rem 0;
            border-bottom: 1px solid #11c0ab;
            margin-bottom: 1.6rem;
        }
        p {
            &:last-child {
                padding-top: 2rem;
                margin-top: auto;
            }
        }
        strong {
            color: #f04242;
            margin-left: 0.5rem;
        }
    }
    &__bottom {
        width: 100%;
        padding: 3rem;
        background: #ebf7f5;
        box-shadow: 0px 5px 10px rgba(1, 18, 15, 0.05);
        border-radius: 20px;
        position: relative;
        margin-top: 6.9rem;
        &:after {
            position: absolute;
            content: "";
            width: 100%;
            max-width: 39rem;
            height: 13rem;
            max-height: 29rem;
            top: 0;
            left: 0;
            background: url("../img/icons/marc.svg");
            z-index: -1;
            transform: translate(-0.5rem, -5rem);
            @media (min-width: 1200px) {
                transform: translate(-3.3rem, -5rem);
            }
        }
    }
    .info__txt {
        align-self: center;
    }
}
.advantages-services {
    padding-bottom: 10rem;
    p {
        width: 100%;
        margin-bottom: 0.8rem;

        // &:last-child {
        //     margin-bottom: 5rem;
        // }
    }
    .block__box {
        p {
            margin-bottom: 0;
        }
    }
}
.advantages-prices.advantages-prices-l {
    li {
        @media (min-width: 1200px) {
            max-width: 57rem;
        }
        &:nth-child(2n) {
            @media (min-width: 1200px) {
                margin-right: 0rem;
            }
        }
        &:nth-child(3n) {
            @media (min-width: 1200px) {
                margin-right: 3rem;
            }
        }
        &:last-child {
            margin-right: 0 !important;
        }
    }
}
.advantages-appeal.advantages-prices,
.advantages-appeal.advantages-prices-s {
    ul {
        padding-left: 0;
    }
    li {
        list-style: none;
    }
}
.advantages-prices-s {
    padding-bottom: 10rem;
    p {
        width: 100%;
        em {
            font-style: normal;
            font-weight: normal;

            color: #11c0ab;
        }
    }
    .info {
        padding-bottom: 3rem;
        .info__txt {
            ul {
                li {
                    list-style: none !important;

                    padding-left: 0;
                    margin-bottom: 2rem;
                    button {
                        width: 100%;
                        text-align: center;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 2rem;
                        line-height: 2.7rem;
                        color: #01120f;
                        @media (min-width: 1200px) {
                            text-align: left;
                            display: flex;
                            justify-content: space-between;
                        }
                        &:hover,
                        &:focus {
                            transition: all 0.3s;
                            text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px,
                                rgba(17, 192, 171, 0.4) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px;
                        }
                        strong,
                        span {
                            pointer-events: none;
                        }
                    }
                }
            }
        }
    }
    ul {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 2rem 0 3rem 0;
        width: 100%;
        @media (min-width: 620px) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
        }
        @media (min-width: 852px) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
        }
    }
    li {
        border: 1px solid #11c0ab;
        box-sizing: border-box;
        border-radius: 20px;
        margin-bottom: 3rem;
        width: 100%;
        max-width: 26.3rem;

        transition: all 0.4s;
        &:hover,
        &:focus {
            transition: all 0.4s;
            transform: scale(1.03, 1.03);
            box-shadow: 0px 5px 10px rgba(17, 192, 171, 0.5);
        }
        button {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            align-items: center;
            width: 100%;
            padding: 1.7rem 2rem;
            font-style: normal;
            font-weight: bold;
            font-size: 2rem;
            line-height: 2.7rem;
            color: #01120f;
            &:hover,
            &:focus {
                transition: all 0.3s;
                text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px,
                    rgba(17, 192, 171, 0.4) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px;
                strong {
                    transition: all 0.3s;
                    text-shadow: #f04242 0.5px 0.5px, #f04242 0.5px 0.5px, #f04242 0.5px 0.5px,
                        rgba(1, 18, 15, 0.7) 0.5px 0px, rgba(1, 18, 15, 0.7) 0.5px 0.5px, rgba(1, 18, 15, 0.7) 0.5px 0px,
                        rgba(1, 18, 15, 0.7) 0.5px 0.5px;
                }
            }
            strong,
            span {
                pointer-events: none;
            }
        }
        h3 {
            margin-left: 3rem;
        }
        strong {
            color: #f04242;
            display: inline-block;
            margin-left: 1rem;
            transition: all 0.3s;
        }
        @media (min-width: 852px) {
            margin-right: 2.93rem;
        }
        @media (min-width: 1230px) {
            margin-right: 3.9rem;
        }
        &:nth-child(4) {
            @media (min-width: 1200px) {
                margin-right: 0rem;
            }
        }
    }
    h2 {
        text-align: left;
        margin-bottom: 3rem;
        width: 100%;
        padding-left: 0;
        &::after {
            display: none;
        }
    }
    .info {
        ul,
        li {
            width: 100%;
            max-width: 100%;
            margin: 0;
        }
        ul {
            padding: 0;
        }
        li {
            border: 1px solid #11c0ab;
            box-sizing: border-box;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;
            margin-right: 0;
            &::before {
                display: none;
            }
        }
    }
    .info__txt {
        align-self: center;
    }
}

