                color: #01120f;
                &:hover,
                &:focus {
                    transition: all 0.3s;
                    text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px,
                        rgba(17, 192, 171, 0.4) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px;
                    strong {
                        transition: all 0.3s;
                        text-shadow: #f04242 0.5px 0.5px, #f04242 0.5px 0.5px, #f04242 0.5px 0.5px,
                            rgba(1, 18, 15, 0.7) 0.5px 0px, rgba(1, 18, 15, 0.7) 0.5px 0.5px,
                            rgba(1, 18, 15, 0.7) 0.5px 0px, rgba(1, 18, 15, 0.7) 0.5px 0.5px;
                    }
                }
                strong,
                span {
                    pointer-events: none;
                }
            }
        }
        h3 {
            margin-bottom: 0;
            padding: 0;
            border-bottom: none;
            margin-right: 3rem;
        }
