.article {
    width: 100%;
    padding: 5rem 0;
    @media (min-width: 1200px) {
        padding: 10rem 0;
    }
    h1 {
        color: #01120f;
        margin-bottom: 5rem;
    }
    &__flex {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        @media (min-width: 1200px) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
    .publications__content {
        margin-bottom: 5rem;
        padding: 0;
        transform: scale(1);
        transition: all 0.3s;
        max-width: 55.6rem;
        @media (min-width: 900px) {
            align-self: stretch;
        }
        @media (min-width: 1300px) {
            padding-right: 1rem;
            padding-bottom: 2rem;
        }
        &:focus,
        &:hover {
            @media (min-width: 1300px) {
                transition: all 0.3s;
                transform: scale(1.05, 1.05);
                box-shadow: 0px 5px 10px rgba(1, 18, 15, 0.07);
            }
        }
    }
    .publications__left {
        @media (min-width: 900px) {
            margin-right: 3rem;
        }
    }
    .publications__content-info {
        padding: 0;
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        justify-content: flex-start;
    }
    .tag {
        @media (min-width: 900px) {
            margin-left: 0;
        }
    }
}
.center {
    margin-left: auto;
    margin-right: auto;

    display: flex;
    justify-content: center;
    align-items: center;
}
