.quit-alcohol {
    width: 100%;
    background: #ebf7f5;
    border-radius: 0px 100px 100px 0px;
    padding: 5rem 0;
    p {
        margin-bottom: 3rem;
    }
}
//  стили для why
.why {
    padding: 0rem 0 10rem 0;
    &__flex {
        width: 100%;
        margin-bottom: 2rem;
        @media (min-width: 1024px) {
            display: flex;
        }
    }
    &__img {
        width: 100%;
        max-width: 49.8rem;
        max-height: 30.7rem;
        border-radius: 20px 70px 70px 70px;
        margin: 0 auto;
        margin-bottom: 2rem;
        position: relative;
        padding-left: 2rem;
        @media (min-width: 560px) {
            padding-left: 4.5rem;
        }
        @media (min-width: 1024px) {
            margin-top: 2rem;
        }
        &::before {
            position: absolute;
            content: "";
            background: url("../img/icons/marc.svg");
            width: 100%;
            height: 100%;
            max-width: 31rem;
            max-height: 29rem;
            top: 0;
            left: 0;
            z-index: -1;
            transform: translateY(-4rem);
        }
        @media (min-width: 1024px) {
            margin: 0;
            margin-bottom: 0;
            margin-right: 3rem;
            align-self: center;
        }
    }
    img {
        width: 100%;
        max-width: 45.8rem;
        max-height: 30.7rem;
        border-radius: 20px 70px 70px 70px;
        z-index: 0;
    }
    &__txt {
        @media (min-width: 1024px) {
            max-width: 65.2rem;
        }
    }
    a {
        color: #11c0ab;
        position: relative;
        display: inline-block;
        padding: 0 0.2rem;
        font-size: 1.6rem;
        &::before {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0.1rem;
            background-color: #11c0ab;
            transform: scaleX(1);
            transition: transform 0.5s;
        }
        &:hover,
        &:focus {
            transition: all 0.3s;
            &::before {
                transform: scaleX(0);
            }
        }
    }
    p {
        margin-bottom: 0.8rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}
