.doctors__block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 6rem;
    @media (min-width: 900px) {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
    }
    @media (min-width: 1124px) {
        align-items: center;
    }

    .doctors__block-img {
        width: 100%;
        max-width: 36rem;
        background: #c4c4c4;
        border-radius: 20px 70px 70px 70px;
        margin-bottom: 2rem;
        position: relative;
        z-index: 0;
        @media (min-width: 900px) {
            min-width: 23rem;
            max-width: 23rem;
            margin-left: 3.5rem;
            margin-right: 5rem;
        }
        @media (min-width: 1124px) {
            min-width: 36rem;
            max-width: 36rem;
            min-height: 34.5rem;
            max-height: 34.5rem;
            margin-left: 5rem;
            margin-right: 5rem;
        }

        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 80%;
            max-height: 29rem;
            background: url("../img/icons/marc.svg");
            z-index: -1;
            transform: translateY(-20%);
            @media (min-width: 460px) {
                max-width: 31rem;
                transform: translate(-5%, -20%);
            }
            @media (min-width: 520px) {
                max-width: 31rem;
                transform: translate(-15%, -20%);
            }
        }
    }
    img {
        width: 100%;
        height: 100%;
        border-radius: 20px 70px 70px 70px;
        object-fit: contain;
        @media (min-width: 900px) {
            min-width: 23rem;
            max-width: 23rem;
        }
        @media (min-width: 1124px) {
            min-width: 36rem;
            max-width: 36rem;
            min-height: 34.5rem;
            max-height: 34.5rem;
        }
    }
    .doctors__block-txt {
        width: 100%;
        max-width: 65rem;
    }
    h3 {
        margin-bottom: 2rem;
    }
    p {
        margin-bottom: 2rem;
    }
    blockquote {
        position: relative;
        padding: 1rem 4rem;
        margin: 0;
        margin-bottom: 3rem;
        &::before {
            position: absolute;
            content: "";
            width: 2.5rem;
            height: 2.5rem;
            background-image: url("../img/icons/left-quote.svg");
            left: 0;
            top: 0;
        }
        &::after {
            position: absolute;
            content: "";
            width: 2.5rem;
            height: 2.5rem;
            background-image: url("../img/icons/left-quote.svg");
            right: 0;
            bottom: 0;
            transform: rotate(180deg);
        }
    }
    &:nth-child(odd) {
        .doctors__block-img {
            margin-bottom: 5rem;
            &::before {
                position: absolute;
                content: "";
                width: 100%;
                height: 80%;
                right: 0;
                bottom: 0;
                max-height: 29rem;
                background: url("../img/icons/marc.svg");
                z-index: -1;
                transform: translateY(20%);
                @media (min-width: 460px) {
                    max-width: 31rem;
                    transform: translate(5%, 20%);
                }
                @media (min-width: 520px) {
                    max-width: 31rem;
                    transform: translate(15%, 20%);
                }
            }
            @media (min-width: 900px) {
                margin-bottom: 2rem;
            }
        }
    }
}
.doctors__block-services {
    margin-bottom: 0;
    .doctors__block-box {
        max-width: 43rem;
        padding-left: 3.5rem;
        .doctors__block-img {
            position: relative;
            @media (min-width: 1024px) {
                margin-left: 0;
            }
            &::before {
                position: absolute;
                content: "";
                width: 100%;
                height: 80%;
                max-height: 29rem;
                background: url("../img/icons/marc.svg");
                z-index: -1;
                left: 0;
                top: 0;
                transform: translateY(-20%);
                @media (min-width: 460px) {
                    max-width: 31rem;
                    transform: translate(-5%, -20%);
                }
                @media (min-width: 520px) {
                    max-width: 31rem;
                    transform: translate(-15%, -20%);
                }
            }
        }
    }
}
