.accordion-first {
    width: 100%;
    article {
        box-sizing: border-box;
        width: 100%;
        border: 1px solid #11c0ab;
        border-radius: 20px;
        margin-bottom: 3rem;
        iframe,
        video {
            width: 100%;
        }
    }
    h3 {
        position: relative;
        z-index: 2;
        display: block;
        font-feature-settings: "pnum" on, "lnum" on;
        transition: all 0.4s;
        cursor: pointer;
        transition: all 1s;
        padding: 1.6rem 3.5rem 1.6rem 2rem;
        font-size: 1.4rem;
        @media (min-width: 600px) {
            font-size: 2rem;
            padding: 1.6rem 5.5rem 1.6rem 2rem;
        }
        &::after {
            position: absolute;
            content: "➔";
            top: calc(40% - 0.3rem);
            right: 0;
            font-weight: bold;
            margin-right: 1.5rem;
            transition: all 0s;
            transform: rotate(90deg);
            margin-top: auto;
            margin-bottom: auto;
            @media (min-width: 960px) {
                top: calc(40% - 0.5rem);
                margin-right: 3.5rem;
            }
        }
        &::before {
            content: "";
            position: absolute;
            width: 88%;
            height: 0.1rem;
            bottom: 0;
            display: inline-block;
            margin-left: auto;
            margin-right: auto;
            background-color: rgba(17, 192, 171, 0.7);
            transform: scale(0);
            transition: all 0.9s;
            @media (min-width: 460px) {
                width: 91%;
            }
            @media (min-width: 560px) {
                width: 92%;
            }
            @media (min-width: 660px) {
                width: 94%;
            }
            @media (min-width: 760px) {
                width: 95%;
            }
        }
    }

    .accordion__item {
        transition: all 0.8s linear;
        h3 {
            transition: all 0.3s linear;
        }
        p {
            padding-left: 3rem;
            padding-right: 3rem;
            overflow: hidden;
            width: 100%;
            height: 0;
            transition: all 0.5s linear;
            font-size: 1.4rem;
            @media (min-width: 600px) {
                font-size: 1.6rem;
            }
        }
    }
    .accordion__content--active {
        margin-bottom: 3rem;
        margin-top: 3rem;
    }
    .accordion__item--active {
        margin-bottom: 3rem;
        padding-bottom: 1rem;

        color: #11c0ab;
        &::after {
            color: #11c0ab;
        }
        &::before {
            transform: scale(1);
            transition: all 0.9s;
        }
        &::after {
            top: calc(40% - 0.3rem);

            transition: all 0s;
            transform: rotate(-90deg);
            @media (min-width: 960px) {
                top: calc(40% - 0.5rem);
                margin-right: 3.5rem;
            }
        }
    }
