.questions-region {
    width: 100%;
    padding-bottom: 10rem;
    ul,
    li,
    h3,
    p {
        width: 100%;
        min-width: 100%;
        max-width: 100%;
    }
    li {
        border: 1px solid #11c0ab;
        box-sizing: border-box;
        border-radius: 20px;
        padding: 0rem 3rem 2rem 3rem;
        margin-bottom: 3rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
    h3 {
        padding: 1.6rem 0;
        border-bottom: 1px solid rgba(17, 192, 171, 0.7);
        margin-bottom: 3rem;
    }
    p {
        margin-bottom: 1rem;
    }
    ul {
        ul {
            margin-bottom: 1rem;
            li {
                border: none;
                position: relative;
                padding: 0;
                padding-left: 2rem;
                margin-bottom: 0;
                &:before {
                    position: absolute;
                    content: "";
                    width: 0.5rem;
                    height: 0.5rem;
                    border-radius: 50%;
                    left: 0.7rem;
                    background-color: #01120f;
                    top: 0;
                    transform: translateY(1.2rem);
                }
            }
        }
    }
    a {
        font-style: normal;
        font-weight: normal;
        font-size: 1.6rem;
        line-height: 170%;
        color: #000;
        display: inline-block;
        position: relative;
        transition: all 0.3s;

        &::before {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0.1rem;
            background-color: #11c0ab;
            transform: scaleX(1);
            transition: transform 0.5s;
        }
        &:hover,
        &:focus {
            transition: all 0.3s;
            text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(17, 192, 171, 0.4) 1px 1px, rgba(17, 192, 171, 0.4) 1px 0px,
                rgba(17, 192, 171, 0.4) 1px 1px;
            &::before {
                transform: scaleX(0);
            }
        }
    }
}
.phone-style {
    a {
        color: #f04242;
        transition: all 0.3s;
        &:hover,
        &:focus {
            transition: all 0.3s;
            text-shadow: #f04242 1px 0px, #f04242 1px 1px, rgba(1, 18, 15, 0.7) 0px 0.5px,
                rgba(1, 18, 15, 0.7) -0.5px 0.5px, rgba(1, 18, 15, 0.7) -0.5px 0px, rgba(1, 18, 15, 0.7) -0.5px -0.5px,
                rgba(1, 18, 15, 0.7) 0px -0.5px, rgba(1, 18, 15, 0.7) 0.5px -0.5px;
        }
        &::before {
            display: none;
        }
    }
}
