.tab {
    width: 100%;
    .tabs__nav {
        a {
            min-width: auto;
            max-width: auto;
            margin: 0 auto;
            padding: 0.5rem;
            text-decoration: none;
            text-align: center;
            // flex-shrink: 0;
            // flex-grow: 1;
            font-style: normal;
            font-weight: bold;
            font-size: 2rem;
            line-height: 2.7rem;
            text-align: center;
            color: #11c0ab;
            margin-bottom: 3rem;
            position: relative;
            transition: all 0.3s;
            align-self: baseline;
            position: relative;
            transition: all 0.3s;
            box-shadow: inset 3px 3px 3px rgba(1, 18, 15, 0);
            &::before {
                position: absolute;
                content: "";
                bottom: 0;
                left: 0;
                width: 100%;
                height: 0.1rem;
                background-color: #11c0ab;
                transition: transform 0.5s;
                transform: scaleX(0);
                @media (min-width: 1100px) {
                    display: none;
                }
            }
            &:hover,
            &:focus {
                transition: all 0.3s;
                &::before {
                    transform: scaleX(1);
                }
            }

            @media (min-width: 620px) {
                margin: 0;
                margin-bottom: 3rem;
                margin-right: 3rem;
            }

            @media (min-width: 1100px) {
                min-width: auto;
                max-width: auto;
                padding: 3rem 0.75rem;
                margin-bottom: 0;
                margin-right: 0;
                flex-shrink: 0;
                flex-grow: 1;
                // background-color: rgba(17, 192, 171, 0.75);
                border: 1px solid rgba(17, 192, 171, 1);
                font-size: 2.1rem;
                line-height: 2.9rem;
            }

            &:first-child {
                @media (min-width: 1100px) {
                    border-radius: 100px 0px 0px 100px;
                }
            }
            &:last-child {
                @media (min-width: 1100px) {
                    border-radius: 0px 100px 100px 0px;
                }
            }
            &:before {
                @media (min-width: 1100px) {
                    position: absolute;
                    content: "";
                    width: 0.1rem;
                    height: 5rem;
                    right: 0;
                    top: 2rem;
                    background-color: rgba(255, 255, 255, 0.8);
                }
            }
            &:last-child {
                @media (min-width: 1100px) {
                    &:before {
                        display: none;
                    }
                }
            }
            &:hover,
            &:focus {
                @media (min-width: 1100px) {
                    transition: all 0.3s;
                    box-shadow: inset 3px 3px 3px rgba(1, 18, 15, 0.1);
                    text-shadow: rgba(1, 18, 15, 0.7) 1px 0px, rgba(1, 18, 15, 0.7) 1px 1px;
                    background-color: #11c0ab;
                    color: #ffffff;
                }
            }
        }
    }
}
.tabs__nav {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    list-style-type: none;
    justify-content: center;
    align-items: center;
    margin: 0;
    @media (min-width: 620px) {
        flex-direction: row;
        justify-content: space-between;
    }
    @media (min-width: 1100px) {
        flex-direction: row;
        justify-content: flex-start;

        border-radius: 100px;
        // background-color: #11c0ab;
    }
}

.tabs__nav a.tabs__link_active {
    cursor: default;
    position: relative;
    transition: all 0.3s;
    align-self: baseline;
    // margin: 0 auto;
    &::after {
        position: absolute;
        content: "";
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 0.1rem;
        background-color: #11c0ab;
        margin: 0 auto;
        display: block;
        transition: all 0.3s;
        @media (min-width: 1100px) {
            display: none;
        }
    }
    @media (min-width: 1100px) {
        box-shadow: inset 3px 3px 3px rgba(1, 18, 15, 0.1);
        color: #ebf7f5;
        font-size: 2.1rem;
        line-height: 2.9rem;
        text-shadow: 0px 2px 5px rgba(1, 18, 15, 0.3);
        background-color: #11c0ab;
    }
}

.tabs__content {
    padding-top: 5rem;
    i {
        display: inline-block;
        color: #11c0ab;
        line-height: 170%;
        margin-bottom: 3.5rem;
        border-bottom: 1px solid #11c0ab;
    }
    p {
        margin-bottom: 1rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

.tabs__pane {
    opacity: 0;
    visibility: hidden;
    height: 0;
    transition: opacity 0.1s, transform 0.6s;
    transform: rotateY(80deg);
}

.tabs__pane.tabs__pane_show {
    height: 100%;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.1s, transform 0.6s;
    transform: rotateY(0deg);
}
//стили для .quit-alcohol__steps

.quit-alcohol {
    .quit-alcohol__nav {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
    }
    &__steps {
        counter-reset: list;
    }
    &__flex {
        margin-bottom: 4rem;
        @media (min-width: 900px) {
            display: flex;
        }
    }
    &__img {
        width: 100%;
        min-height: 19.3rem;
        max-height: 24.1rem;
        background: #c4c4c4;
        border-radius: 20px;
        align-self: center;
        position: relative;
        margin: 0 auto;
        margin-bottom: 2rem;
        @media (min-width: 420px) {
            max-width: 36rem;
            min-width: 36rem;
            max-height: 24.1rem;
            min-height: 24.1rem;
        }
        @media (min-width: 900px) {
            margin: 0;
            margin-top: 3rem;
            margin-right: 3rem;
        }
        &:before {
            position: absolute;
            counter-increment: list;
            content: counter(list);
            font-style: normal;
            font-weight: bold;
            font-size: 6.4rem;
            line-height: 146.4%;
            color: #f04242;
            top: 0;
            left: 0;
            -webkit-transform: translate(15%, -50%);
            -ms-transform: translate(15%, -50%);
            transform: translate(15%, -50%);
        }
    }
    img {
        width: 100%;
        max-height: 100%;
        min-height: 19.3rem;
        max-height: 24.1rem;
        border-radius: 20px;
        @media (min-width: 420px) {
            max-width: 36rem;
            min-width: 36rem;
            min-height: 24.1rem;
            max-height: 24.1rem;
        }
    }
    &__txt {
        a {
            display: inline-block;
            position: relative;
            padding-bottom: 0.3rem;
            transition: all 0.3s;
            color: #11c0ab;
            &::before {
                position: absolute;
                content: "";
                bottom: 0;
                left: 0;
                width: 100%;
                height: 0.1rem;
                background-color: #11c0ab;
                transform: scaleX(1);
                transition: transform 0.5s;
            }
            &:hover,
            &:focus {
                transition: all 0.3s;
                &::before {
                    transform: scaleX(0);
                }
            }
        }
    }
    h3 {
        // margin-bottom: 2rem;
        margin-bottom: 4rem;
        @media (min-width: 900px) {
            margin-bottom: 2rem;
        }
    }
    button {
        width: 100%;
        max-width: 31.5rem;
        margin: 0 auto;
        display: block;
    }
}
// Кодирование в Ленинградской обл
.encoding-tab {
    padding: 0;
    .quit-alcohol__img {
        @media (min-width: 900px) {
            margin-top: 0rem;
            align-self: flex-start;
        }
        &:before {
            display: none;
        }
    }
}
